<template>
  <div class="container" v-if="bookMsg!==null">
    <div class="head">
      <h2>{{bookMsg.title}}</h2>
    </div>
    <div class="book">
      <div class="img">
        <img :src="bookMsg.image" width="135" alt="">
      </div>
      <div class="detail">
        <div class="author" v-if="bookMsg.author!==undefined">作者: <a :href="bookMsg.image">{{bookMsg.author[0]}}</a>
        </div>
        <div class="publisher" v-if="bookMsg.publisher">出版社: <span>{{bookMsg.publisher}}</span></div>
        <div class="publite">出品方: <a :href="bookMsg.image">新经典文化</a></div>
        <div class="origin_title" v-if="bookMsg.origin_title">原作名: <span>{{bookMsg.origin_title}}</span></div>
        <div class="translator" v-if="bookMsg.translator.length!==0">译者: <a :href="bookMsg.image">{{bookMsg.translator[0]}}</a>
        </div>
        <div class="pubdate" v-if="bookMsg.pubdate">出版年: <span>{{bookMsg.pubdate}}</span></div>
        <div class="pages" >页数: <span>{{bookMsg.pages}}</span></div>
        <div class="price" >定价: <span>{{bookMsg.price}}</span></div>
        <div class="price" v-if="bookMsg.binding">装帧: <span>{{bookMsg.binding}}</span></div>
        <div class="ISBN" >ISBN: <span>{{bookMsg.isbn13}}</span></div>
      </div>
      <div class="star">
        <span>豆瓣评分</span>
        <div>
          <div class="avg">{{bookMsg.rating.average}}</div>
          <div class="numRaters">{{bookMsg.rating.numRaters}}人评价</div>
        </div>
      </div>
    </div>
    <div id="content">
      <h3 class="summary">内容简介· · · · · ·</h3>
      <div v-html="summary"></div>

      <h3 class="author_intro">作者简介· · · · · ·</h3>
      <div v-html="author_intro"></div>
      <h3 class="catalog">目录· · · · · ·</h3>
      <div v-html="catalog"></div>
      <h3 class="tags">豆瓣成员常用的标签(共73个)  · · · · · ·</h3>
      <div class="tagname">
        <a :href="bookMsg.image" v-for="item in bookMsg.tags">{{item.name}}</a>
      </div>
    </div>
</div>

</template>

<script>
  export default {
    name: "find-book",
    data() {
      return {
        bookMsg: null,
        summary: '',
        author_intro:'',
        catalog:''

      }
    },
    created() {
      this.$jsonp('https://api.douban.com/v2/book/' + this.$route.params.id).then(data => {
        console.log(data)
        this.bookMsg = data;
        this.summary = addP(data.summary);
        this.author_intro = addP(data.author_intro);
        this.catalog = addP(data.catalog);
      })
    }
  }

  function addP(text) {
    let arr = text.split('\n'), html = '';
    arr.map((val, index) => {
      if (index !== 0) {
        html += `<p class="contP">${val}</p>`;
      }
    })
    return html;
  }
</script>

<style scoped>
  .container {
    padding-left: 10px;
  }

  .head h2, h3 {
    text-align: left;
  }

  .book {
    height: 200px;
    color: #666;
    font-size: 13px;
    line-height: 1.6;
    text-align: left;
  }

  .detail {
    margin-left: 8px;
  }

  .img, .detail {
    float: left;
  }

  .star {
    float: right;
    margin-right: 200px;
    border-left: 1px solid #ccc;
    padding-left: 5px;
  }

  .avg {
    font-size: 28px;
    color: #494949;
  }
  .summary,.author_intro,.catalog,.tags{
    color:#007722;
    font-size: 18px;
    font-weight: normal;
  }
  .tagname a{
    margin-right: 15px;
  }
</style>



