<template>
  <div class="box">
    <div class="content">
      <h3>搜索结果</h3>
      <ul>
        <li v-for="item in allMsg">
          <img :src="item.image">
          <div class="bookdes">
            <p><a @click="findBook(item.id)">{{item.title}}</a></p>
            <P><span style="color:#f9b70f;">{{star(item.rating.average)}}</span> <span style="font-size: 14px">{{item.rating.average}} ({{item.rating.numRaters}}人评价)</span></P>
            <span> {{item.author[0]}}/ {{item.publisher}} / {{item.pubdate}} / {{item.price}}</span>
          </div>
        </li>
      </ul>

    </div>
  </div>

</template>

<script>
  export default {
    name: 'findBook',
    data() {
      return {
        allMsg: null,
        stars:"★★★★★☆☆☆☆☆"
      }
    },
    watch: {
      '$route':'keyChnage'
    },
    methods: {
      keyChnage: function () {
        this.$jsonp(`https://api.douban.com/v2/book/search?q=${this.$route.query.key}`).then(data => {
          this.allMsg=data.books;
        });
      },
      findBook(id) {
        this.$router.push({path:'/find/'+id});
      },
      star(num){
        console.log(num)
        if(num>0&&num<=2){
          return this.stars.slice(4,9);
        }else if(num>2&&num<=4){
          return this.stars.slice(3,8);
        }
        else if(num>4&&num<=6){
          return this.stars.slice(2,7);
        }
        else if(num>6&&num<=8){
          return this.stars.slice(1,6);
        }else if(num==0){
          return this.stars.slice(5,10);
        }else{
          return this.stars.slice(0,5);
        }
      }
    },
    created() {
      this.$jsonp(`https://api.douban.com/v2/book/search?q=${this.$route.query.key}`).then(data => {
        this.allMsg=data.books;
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  li{
  height: 148px;
  margin-bottom: 10px;
  border-bottom: 1px #ccc dashed;
}
  h3{
    text-align: left;
  }
  img {
    float: left;
    width: 99px;
    height: 148px;
  }
  .bookdes {
    height: 148px;
    float: left;
    margin-left: 15px;
    text-align: left;
  }
  .bookdes span{
    margin-bottom: 5px;
  }
  .content{
    padding-left: 20px;
  }
  .content p a{
    color: deepskyblue;
    text-decoration: underline;
    cursor: pointer;
  }
  .content p a:hover{
    color:red;
  }
</style>



