import Vue from 'vue'
import Router from 'vue-router'
import searchBook from '@/components/searchBook'
import findBook from '@/components/findBook'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/search',
      component: searchBook
    },
    {
      path: '/find/:id',
      component: findBook
    },
  ]
})



