<template>
  <div id="app">
    <div class="search">
      <label for="search">豆瓣读书: </label><input id="search" type="text" placeholder="书名、作者、ISBN" v-model="bookName">
      <button @click="searchBook">搜索</button>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      bookName: ''
    }
  },
  methods: {
    searchBook() {
      if(this.bookName.trim()!==''){
        this.$router.push({path:'/search/?key='+this.bookName});
      }

    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
  #app {
    width: 960px;
    border: 1px solid #ccc;
    margin: 0 auto;
    padding: 20px 10px;
  }
.search {
  width: 500px;
  height: 30px;
  margin: 0 auto;
}

.search label {
  float: left;
  font-size: 20px;
}

.search input {
  text-indent: 5px;
  padding: 2px;
  height: 20px;
  width: 200px;
  float: left;
  margin-left: 10px;
}

.search button {
  height: 28px;
  font-size: 16px;
  float: left;
  margin-left: 10px;
}
.contP{
    text-align: left;
    text-indent: 2em;
    font-size: 15px;
  }
</style>



